    top 0;
    width 100%;
    z-index: 20;
  }

  .header .back {
    height: .34rem;
    line-height: .34rem;
    border-bottom: 0;
    padding-bottom 0.1rem;
    position: relative;
    background-color: $bgcolor;
  }

  .header .back span {
    font-size: 0.18rem;
    width: .4rem;
    height: .44rem;
    position: absolute;
    top: 0;
    left: 0;
    text-align center;
    line-height 0.44rem;
    color: #fff;
  }

  .header .back h1 {
    overflow: hidden;
    margin: 0 0.5rem;
    line-height: .44rem;
    height 0.44rem;
    white-space: nowrap;
    text-overflow: ellipsis;
    font-size: .16rem;
    text-align: center;
    color: #fff;
  }

  .back-bot {
    height: .3rem;
    line-height: .4rem;
    background-color: $bgcolor;
    color: #fff;
    padding: 0 0.5rem;
  }

  .back-bot ul {
    border: 1px solid #fff;
    border-radius: .03rem;
    zoom: 1;
    overflow: hidden;
  }

  .back-bot li {
    width: 50%;
    line-height: 0.2rem;
    float: left;
    text-align: center;
    background-color: $bgcolor;
    font-size: .14rem;
    color: #fff;
  }

  .back-bot li:nth-child(1) {
    background-color #fff;
    color: #00bcd4
  }

  //hot部分
  .hot {
    padding-top: 0.74rem;
    width: 100%
  }

  .hot h2 {
    font-size: .12rem;
    padding: .12rem .15rem;
    color: #212121;
  }

  .hot ul {
    width: 100%;
    background-color: #fff;
    overflow: hidden;
  }

  .hot ul li {
    box-sizing: border-box;
    width: 33.3%;
    height: .45rem;
    line-height: .45rem;
    font-size: .14rem;
    text-align: center;
    border-bottom: .01rem solid #ddd;
    border-right: .01rem solid #ddd;
    margin-bottom: -0.5px;
    float: left;
    position: relative;
    z-index: 10;
    color: #212121;
  }

  .hot ul li:nth-child(3n) {
    border-right: none;
  }

  .hot ul li a {
    color: #212121;
    display: block;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    font-size: .14rem;
  }

  //sort部分
